html, body {
  height: 100%;
  overflow-x: hidden;
}

body {
  font-family: $font-regular;
  font-size: 16px;
  -webkit-overflow-scrolling: touch !important;
  line-height: 20px;
  &.modal-open {
        overflow: hidden !important;
        position: fixed;
        width: 100%;
        &:before {
            content: '';
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            // background-color: rgba(black, 0.5);
            position: fixed;
            z-index: 99;
            // @include zindex(50);
        }
        &.open-modal-video:before {
            background-color: black;
        }
    }

  @include respond-to(320) {
    font-size: 15px;
  }
  @include respond-to(480) {
    font-size: 15px;
  }
}

.common {
  padding: $common-indent $common-indent 0;
}

