}

.push-right {
  float: right;
}
.push-left {
  float: left;
}

.modal.active {
    display: block !important;
}

.modal {
    @extend .animated;
    animation-duration: .3s;
    position: fixed;
    width: 100%;
    max-height: 92%;
    height: 100%;
    background: white;
    left: 0;
    bottom: 0;
    border-radius: 0;
    display: none;
    overflow: hidden;
    z-index: 99;
    box-shadow: 0 0 15px 7px rgba(0, 0, 0, 0.5);
    & .modal-button {
      background: #f0f0f0;
      height: 47px;
      padding: 12px 0 0 0;
      cursor: pointer;
      box-sizing: border-box;
      text-align: center;
      &:after {
        content: '';
        display: inline-block;
        width: 24px;
        height: 24px;
        background: url('../img/svg/cross-black.svg') no-repeat;
        background-size: cover;
      }
    }
    & .modal-content {
        padding: 0.938em;
        overflow-y: scroll;
        box-sizing: border-box;
        height: calc(100% - 47px);
        &.menu {
            display: flex;
            // justify-content: center;
            // align-items: center;
            justify-content: flex-start;
            align-items: flex-start;
            height: 92%;
        }
    }
    & .loading {
        font-size: 1.1em;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $text-grey;
    }
}

.faq-page .modal {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  left: 0;
  top: 0;
  display: none;
  overflow: hidden;
  z-index: 99;
  border-radius: 0;
  & .modal-button {
    z-index: 100000;
    position: absolute;
    right: 0;
    height: 55px;
    background: rgba(0,0,0,0.2);
    padding: 15px 15px 0 0;
    cursor: pointer;
    box-sizing: border-box;
    text-align: right;
    left: 0;
    &:after {
      width: 25px;
      height: 25px;
      background: url('../img/svg/cross-out.svg') no-repeat;
      background-size: cover;
    }
  }
  & .modal-content {
    padding: 0;
    height: 100%;
  }
}

